.user_profile {
    padding: 20px;
    background-image: url('./images/Image4.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    z-index: 500;
    margin-top: -20vh;

    .content {
        background-color: rgba(255, 255, 255, 0.623);
        backdrop-filter: blur(2px);
        box-shadow: 0px 3px 12px grey;
        padding: 40px;
        display: grid;
        grid-template-rows: auto auto auto auto auto;
        width: 40vw;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20vh;
        border-radius: 10px;
        height: 40vh;

        .titre {}

        .input_connexion {
            margin-bottom: 20px;
        }
    }

}


@media only screen and (max-width: 600px) {

    .add_article {

        .content {
            width: 70vw;
            

            .titre {}

            .input_connexion {
                margin: 10px;
            }
        }
    }

}