.container_2 {
  display: grid;
  grid-template-columns: 50% 50%;
  padding-top: 20vh;
  margin-left: auto;
  margin-right: auto;
  width: 80vw;
}

@media only screen and (max-width: 600px) {
  .container_2 {
    margin-top: 20%;
    display: contents;
    grid-template-columns: 100%;
    width: 90vw;
  }
  .container_2 .box {
    margin: 20px;
    width: 90vw;
  }
}/*# sourceMappingURL=Container_2_style.css.map */