.form_connexion {
    padding: 20px;
    background-image: url('./images/Image4.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    z-index: 500;
    margin-top: -20vh;

    .content {
        background-color: rgba(255, 255, 255, 0.623);
        backdrop-filter: blur(2px);
        box-shadow: 0px 3px 12px grey;
        padding: 40px;
        display: grid;
        grid-template-rows: repeat(4, calc(100% * 1/4));
        width: 30vw;
        margin-left: auto;
        margin-right: auto;
        margin-top: 30vh;
        border-radius: 10px;
        height: 40vh;

        .titre {
            
        }

        .input_connexion {
            margin-bottom: 20px;
        }
    }

}

@media only screen and (max-width: 600px) {

    .form_connexion {

        .content {
            box-shadow: 0px 3px 12px grey;
            padding: 20px;
            display: grid;
            grid-template-rows: repeat(4, calc(100% * 1/4));
            width: 80vw;
            margin: auto;
            margin-top: 30vh;
            height: 50vh;

            .titre {}

            .input_connexion {
                margin: 10px;
            }
        }
    }

}