.navbar {
  cursor: pointer;
  box-shadow: 0 5px 10px rgb(209, 209, 209);
  height: auto;
  z-index: 1;
  position: sticky;
  top: 0;
  background-color: white;
  display: grid;
  grid-template-columns: 20% auto;
}
.navbar .titre_page {
  padding: 2px;
  font-size: 30px;
  font-family: "Blackadder ITC";
  color: #f50000;
}
.navbar ul {
  display: flex;
  margin: 0;
}
.navbar ul li {
  list-style-type: none;
  padding: 10px;
  border-bottom: 3px solid rgb(255, 255, 255);
  transition: 0.25s;
}
.navbar ul li:hover,
.navbar ul .active {
  border-bottom: 3px solid rgb(85, 85, 85);
}

.banner {
  position: sticky;
  top: 0;
  background-color: #FF6D6D;
  padding: 30px;
  margin-left: auto;
  margin-right: auto;
  width: 150px;
  color: white;
  font-weight: bolder;
  font-style: italic;
  z-index: 1;
  transition: 0.25s;
}

.connexion_add {
  position: fixed;
  right: 50px;
  top: 10%;
  z-index: 1;
  transition: 0.25s;
  cursor: pointer;
  z-index: 1;
  color: #FF6D6D;
}

.connexion_add:hover {
  transform: scale(1.2);
}

.new_article_dialogue {
  padding: 10px;
}
.new_article_dialogue .input_new_article {
  width: 100%;
  margin: 5px;
}
.new_article_dialogue .input_new_article_file {
  width: 100%;
  margin: 5px;
  border: 1px solid rgb(199, 199, 199);
}

@media only screen and (max-width: 600px) {
  .navbar {
    z-index: 1;
    display: grid;
    grid-template-columns: 50% auto;
  }
  .navbar ul {
    text-align: left;
  }
  .banner {
    display: none;
  }
}/*# sourceMappingURL=Navbar_style.css.map */